@import "./../App/vars";

.cart-footer {
  display: grid;
  grid-template-columns: 5fr 3fr 4fr;
  column-gap: 30px;

  padding: 19px 0;
  background: #EAEFF2;

  font-weight: 700;
  font-size: 18px;
  line-height: 1.2;

  @media (max-width: $mobile) {
    grid-template-columns: 1fr 1fr;
    text-align: center;
    font-size: 16px;
  }

  @media (max-width: $mobile-xs) {
    grid-template-columns: 1fr;
    row-gap: 15px;
    font-size: 16px;
  }
}

.cart-footer__count {
  grid-column-start: 2;

  @media (max-width: $mobile) {
    grid-column-start: 1;
  }
}